import React from 'react'
import {IoIosArrowRoundDown} from 'react-icons/io'

 
const HeroSection = () => {
    return (
        <section className="grid w-full min-h-screen place-items-center">
            <div className="relative flex flex-col justify-center text-center responsive">
                <h1 className="mb-4 text-6xl font-bold font-play">Indra <br /> Prakoso <br /> Poerwanto</h1>
                <h2 className="font-medium text-ip-gray-69">Full-Stack Designer</h2>
            </div>
            <div className="absolute left-0 right-0 grid w-full bottom-5 justify-items-center">
                <div className="animate-bounce"><IoIosArrowRoundDown className="text-4xl"/></div>
            </div>
        </section>
    )
}

export default HeroSection
