import React, { useState } from 'react'
import { Link} from 'react-router-dom'
import './Navbar.css'
import { IoMenuOutline } from 'react-icons/io5'


const Navbar = () => {

    const navLinks = [
        {
            title: 'Home',
            linkTo: '/'
        },
        {
            title: 'Portfolio',
            linkTo: '/portfolio'
        },
        {
            title: 'Info',
            linkTo: '/info'
        }
    ]
    
    const chgNav = (element) => {
        let navLink = document.getElementsByClassName('nav-link')
        for (let i = 0; i < navLink.length; i++) {
            navLink[i].classList.remove('nav-link-active')     
        }
        element.target.classList.add('nav-link-active')    
    }

    const [openNav, setOpenNav] = useState(false)
        
    const toggleNav = () => {
        let navLinks = document.getElementById('nav_links')
        if (openNav===false) {
            setOpenNav(true);
            navLinks.style.maxHeight = navLinks.scrollHeight + "px";
        } else {
            setOpenNav(false);
            navLinks.style.maxHeight = null;
        }
        
    }

    return (
        <div className="relative flex items-center w-full h-20">
            <div className="flex items-center responsive">
                <Link to='/'>
                    <img className="h-10 " src="./assets/img/logo.svg" alt="" />
                </Link>
                <div className="ml-auto">
                    <span className="relative grid w-10 h-10 text-4xl place-items-center md:hidden" onClick={()=> toggleNav()}>
                        <IoMenuOutline/>
                        <div className="absolute inset-0 w-full h-full"></div>
                    </span>
                    <div id="nav_links" className="">
                        <ul className="flex flex-col items-end h-full py-5 space-y-2 responsive md:flex-row md:space-y-0 md:space-x-6 md:py-0">
                            {
                                navLinks.map((link, index) => 
                                    <li key={index} className="text-sm font-semibold"><Link to={link.linkTo} className={`nav-link ${link.linkTo === window.location.pathname ? 'nav-link-active' : ''}`} onClick={(e)=> chgNav(e)} >{link.title}</Link></li>
                                )
                            }
                        </ul>
                    </div>
                    
                </div>
            </div>
        </div>
    )
}

export default Navbar
